<template>
  <div>
    <canvas id="canvas" width="300" height="480">
    </canvas>
    <div id="control-box">
      <a class="control" v-for="n in 12" :data-value="n" href=# @click="controlClicked">
      {{n}}
      </a>
    </div>   
    <div id="game-over" :class="{'hide': !gameOverShow,
                                 'show': gameOverShow}"
                       @click="gameOverClicked">
    </div>
  </div>
</template>

<script>
 export default {
   data () {
     return {
       stage: null,
       canvas: null,
       boxWidth: 50,
       boxHeight: 20,
       gameWidth: 300,
       gameHeight: 480,
       createjs: null,
       numberBoxes: [],
       fallingSpeed: 0.8,
       ticksPerNewBox: 80,
       calculationText: null,
       controlHeight: 100,
       inputs: [],
       result: 1,
       boundaryY: 320,
       initialLifes: 3,
       lifes: 3,
       hearts: [],
       heartsContainer: null,
       gameOverShow:false
     }
   },

   mounted () {
     this.init()
     this.generateNumberBox()
     this.stage.update()
   },

   methods: {
     init () {
       this.createjs = window.createjs
       this.canvas = document.getElementById('canvas')
       this.stage = new this.createjs.Stage(this.canvas)
       this.createjs.Ticker.setFPS(40)
       this.createjs.Ticker.addEventListener('tick', this.tick)

       this.calculationText = new this.createjs.Text('1X1=1', '18px Impact', 'White')
       this.calculationText.textAlign = 'center'
       this.calculationText.x = this.gameWidth / 2
       this.calculationText.y = this.gameHeight - this.controlHeight - 30
       this.stage.addChild(this.calculationText)

       var line = new this.createjs.Bitmap('../../static/images/line.png')
       line.y = this.boundaryY
       this.stage.addChild(line)

       this.initHearts()
     },
     initHearts () {
       this.heartsContainer = new this.createjs.Container()
       this.heartsContainer.x = 5
       this.heartsContainer.y = 5
       this.stage.addChild(this.heartsContainer)

       this.resetHearts()
     },
     resetHearts () {
       this.heartsContainer.removeAllChildren()
       this.hearts.length = 0
       for (var i = 0; i < this.initialLifes; i++) {
         var heart = new this.createjs.Bitmap('../../static/images/heart.png')
         heart.x = i * 20
         this.heartsContainer.addChild(heart)
         this.hearts.push(heart)
       }
     },
     deduceLife () {
       this.lifes -= 1
       var heart = this.hearts[this.lifes]
       this.heartsContainer.removeChild(heart)
       if (this.lifes <= 0) {
         this.gameOver()
       }
     },
     gameOver () {
       this.createjs.setPaused(true)
     },
     updateText (string) {
       this.calculationText.text = string
     },
     controlClicked (e) {
       var value = e.target.dataset.value
       var string = this.addInput(value)
       this.updateText(string)
       this.checkResult()
     },
     addInput (value) {
       if (this.inputs.length >= 2) {
         this.clearInputs()
       }

       this.inputs.push(value)
       this.result *= value
       return this.inputs.join('X') + '=' + this.result
     },
     clearInputs () {
       this.inputs.length = 0
       this.result = 1
     },
     tick (e) {
       this.stage.update()
       if (!e.paused) {
         this.moveObjects()
         var ticksCount = this.createjs.Ticker.getTicks(true)
         if (ticksCount % this.ticksPerNewBox === 0) {//每两秒有一个盒子下落
           this.generateNumberBox()
         }
       }
     },
     moveObjects () {
       var p = 0
       while (p < this.numberBoxes.length) {
         var box = this.numberBoxes[p]
         if (box.y > this.boundaryY) {
           this.removeNumberBox(box)
           this.deduceLife()
         } else {
           box.y += this.fallingSpeed
           p++
         }
       }
     },
     removeNumberBox (target) {
       for (var i = 0, len = this.numberBoxes.length; i < len; i++) {
         var box = this.numberBoxes[i]
         if (box === target) {
           this.numberBoxes.splice(i, 1)
           this.stage.removeChild(box)
           return
         }
       }
     },
     findNumberBoxWithValue (value) {
       for (var i = 0, len = this.numberBoxes.length; i < len; i++) {
         var box = this.numberBoxes[i]
         if (box.value === value) {
           return box
         }
       }
     },
     checkResult () {
       var box = this.findNumberBoxWithValue(this.result)
       if (box) {
         this.showCircle(box.x, box.y)
         this.removeNumberBox(box)
         this.clearInputs()
       }
     },
     showCircle (x, y) {
       var circle = new this.createjs.Bitmap('../../static/circle.png')
       circle.x = x || 0
       circle.y = y || 0
       this.stage.addChild(circle)
       this.createjs.Tween.get(circle).wait(500).to({alpha: 0}, 1000).call(function () { this.stage.removeChild(circle) }.bind(this))
      //  TweenJS类库主要用来调整和动画HTML5和Javascript属性。提供了简单并且强大的tweening接口。
     },
     rectShape (width, height, style) {
       var obj = new this.createjs.Container()
       style = style || {}
       style.strokeWidth = style.strokeWidth || 0
       style.strokeColor = style.strokeColor || 0
       style.fillColor = style.fillColor || 'rgb(255, 255, 0, 1)'

       var shape = new this.createjs.Shape()
       shape.graphics.setStrokeStyle(style.strokeWidth).beginStroke(style.strokeColor).beginFill(style.fillColor).drawRect(0, 0, width, height)
       obj.addChild(shape)
       return obj
     },
     box () {
       var obj = new this.createjs.Container()
       var bitmap = new this.createjs.Bitmap('../../static/images/box.png')
       obj.addChild(bitmap)
       return obj
     },
     numberBox (value) {
       var boxObj = this.box()
       var text = new this.createjs.Text(value, '24px Impact', 'red')
       text.textBaseline = 'top'
       text.textAlign = 'center'
       text.x = this.boxWidth / 2
       if (text.y === undefined) {
         console.log('in numberBox')
       }
       text.y = this.boxHeight / 2
       boxObj.addChild(text)
       boxObj.value = value
       return boxObj
     },
     randomInt (min, max) {
       return Math.floor(Math.random() * (max - min + 1) + min)
     },
     generateNumberBox () {
       var value = this.randomInt(1, 12) * this.randomInt(1, 12)
       var box = this.numberBox(value)
       box.x = Math.random() * (this.gameWidth - this.boxWidth)
       if (box.y === undefined) {
         console.log('generaeNumberBox')
       }
       box.y = 0
       this.stage.addChild(box)
       this.numberBoxes.push(box)//生成的盒子集合，后续扫描即可
     },
     gameOver () {
     this.createjs.Ticker.setPaused(true)
     this.showGameOver()
  },
  showGameOver () {
    this.gameOverShow = true
  },
  hideGameOver () {
    this.gameOverShow = false
  },
  gameOverClicked () {
       this.lifes = this.initialLifes
       this.removeAllNumberBoxes()
       this.hideGameOver()
       this.resetHearts()
       this.createjs.Ticker.setPaused(false)
       console.log(this.gameOverShow)
     },
     removeAllNumberBoxes () {
       for (var i = 0, len = this.numberBoxes.length; i < len; i++) {
         var box = this.numberBoxes[i]
         this.stage.removeChild(box)
       }

       this.numberBoxes.length = 0
     },


   }
 }
</script>

<style scoped>
  #canvas {
    background: #333; 
  }
  #control-box {
    width: 100%;
    overflow: auto;
    position: absolute;
    bottom: 0;
  }

  .control {
    display: block;
    float: left;
    width: 50px;
    height: 50px;
    background: gray;
    text-align: center;
    line-height: 50px;
    font-size: 24px;
    font-family: impact;
    background: gray url(../../static/images/input_button.png);
  }

  .control.active {
    background: white;
    color: red;
  }


.show {
    display: block;
  }

  .hide {
    display: none;
  }

  #game-over {
    background: url(../../static/images/replay.png);    width: 100px;
    height: 60px;
    margin: auto;
    position: absolute;
    left: 100px;
    top: 200px;
  }

  #game-over:hover {
    background-image: url(../../static/images/replay_hover.png);
  }

  #game-over:active {
    background-image: url(../../static/images/replay_active.png);
  }

</style>



