<template>
  <div>
    <header>
      <div class="row">
        <h1>Multiply Defense</h1>
      </div>
    </header>
    <section id="game" class="row">
      <game-scene></game-scene>
    </section>
  </div>
</template>

<script>
  import GameScene from './GameSceneComponent'
  export default {
    components: {
      GameScene
    }
  }
</script>

<style scoped>
  #game {
    position: relative;
    width: 300px;
    height: 480px;
    border: 1px solid black;
  }

</style>



